

























































import ut from '../../utils/index.js';
export default {
	data() {
		return {
			static: '',
			remakeinf: {},
			date: '',
			time: '',
			y: "",
			m: '',
			d: '',
			adress: '',
			type:''
		}
	},
	onLoad(opt) {
		this.type=opt.type;
		this.static = ut.static;
		this.req_getdefaddress();
		this.remakeinf = wx.getStorageSync('remakeinf');
		console.log(JSON.stringify(this.remakeinf))
		wx.setStorageSync('remakeinf', '');
	},
	onShow() {
		if (wx.getStorageSync('adress')) {
			this.adress = wx.getStorageSync('adress');
			wx.setStorageSync('adress', '');
		}
	},
	methods: {
		go_mine_adress() {
			wx.navigateTo({
				url: '../mine/adress?src=pay'
			})
		},
		reform(){
			ut.request({
				data: {
					"addressId": this.adress.id,
					"makeTime": this.date + this.time,
					"remark": '',
					"reformTypeId": this.remakeinf.reformTypeId,
					"houseTypeId": this.remakeinf.houseTypeId
				},
				url: "reform/make"
			}).then(data => {
				wx.showModal({
				title: '提示',
				content: '预约成功',
				showCancel:false,
				success(res) {
					if (res.confirm) {
					wx.navigateBack();
					} 
				}
				})
			})
		},
		whole(){
			ut.request({
				data: {
					"addressId": this.adress.id,
					"makeTime": this.date + this.time,
					"remark": '',
					"wholeHomeTypeId": this.remakeinf.wholeHomeTypeId,
					"houseTypeId": this.remakeinf.houseTypeId,
					"areaTypeId": this.remakeinf.areaTypeId
				},
				url: "renovation/make"
			}).then(data => {
				wx.showModal({
				title: '提示',
				content: '预约成功',
				showCancel:false,
				success(res) {
					if (res.confirm) {
					wx.navigateBack();
					} 
				}
				})
			})
		},
		req_remake() {
			if (!this.adress) {
				ut.totast('请选择地址');
				return;
			}
			if (!this.date) {
				ut.totast('请选择日期');
				return;
			}
			if (!this.time) {
				ut.totast('请选择时间');
				return;
			}
			if(this.type){
				this.whole()
			}else{
				this.reform()
			}
		
		},
		timeChange: function(e) {
			this.time = e.detail.value;
		},
		dateChange: function(e) {
			this.date = e.detail.value;
			const date = this.date.split('-');
			this.y = date[0];
			this.m = date[1];
			this.d = date[2];
		},
		req_getdefaddress() {
			ut.request({
				url: "address/getdefaddress"
			}).then(data => {
				this.adress = data;
			})
		}
	}
}
